import React from 'react';
import Animate from 'react-smooth';

const Skills = () => {
    const skillsArr = [
        {skill: 95, url: 'https://ucarecdn.com/93670477-4578-4816-86c9-d3d787af190c/html.png'},
        {skill: 95, url: 'https://ucarecdn.com/bbc26da8-ecca-4e47-9533-5169a39b49a9/css.png'},
        {skill: 75, url: 'https://ucarecdn.com/7c03bc7d-f7f9-40a8-93b3-0c6ded890b23/js.svg'},
        {skill: 70, url: 'https://ucarecdn.com/d1bf20cf-2073-4649-87e6-0a109515201b/react.png'},
        {skill: 75, url: 'https://ucarecdn.com/9c5b5d07-02cd-467c-b1a7-d925d4036799/bootstrap.png'},
        {skill: 90, url: 'https://ucarecdn.com/628dfc77-c655-4973-a8d4-d6a17ee9b0ba/git.png'},
        {skill: 80, url: 'https://ucarecdn.com/27704dc8-3a98-46f8-aadc-eb5042a19c8c/gulp.png'},
        {skill: 90, url: 'https://ucarecdn.com/4e3129ce-90eb-49d0-a12d-16976c7ca655/sass.png'}
    ];
    const skills = skillsArr.map((item, i) => {
        const {skill, url} = item;
        return (
            <div className="skills-item" key={i}>
                <div className="skills-item__img">
                    <img src={url} alt={i}/>
                </div>
                <div className="skills-item__scale">
                    <div style={{width: skill + '%' }}></div>
                </div>
            </div>
        );
    });

    return (
       <Animate to="1" from="0" attributeName="opacity">
            <section className="skills">
                <div className="container">
                    <h2 className="skills__title title">Skills</h2>
                    <div className="skills__wrapper">
                        {skills}
                    </div>
                </div>
            </section>
       </Animate>
    );
};

export default Skills;